<template>
  <div class="wraper">
    <NavBar></NavBar>
    <Loading :loading="loading"></Loading>
    <div class="content">
      <div class="m-title">
        <h3>豆瓣电影TOP250</h3>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/Nav'
import Loading from '../components/Loading'
export default {
  data () {
    return {
      loading: false,
      url: 'https://api.douban.com/v2/movie/top250?start=',
      movies: {}
    }
  },
  computed: {
    current () {
      return this.$route.query.page ? this.$route.query.page : 1
    }
  },
  components: {
    NavBar,
    Loading
  },
  mounted () {
    this.$http.jsonp(this.url + this.start).then((response) => {
      this.movies = response.body
      this.loading = false
    }, (response) => {
      console.log('请求失败!')
    })
  }
}
</script>

<style lang="scss">
  .m-title {
    width: 100%;
    padding-left: 30px;
    height: 100px;
    background-color: #e3ebec;

    h3 {
      font-size: 35px;
      font-weight: bold;
      color: #37a;
      letter-spacing: 1px;
      line-height: 100px;
    }
  }
</style>



